import '../Form/formulario.css';



function validateForm(form) {

    if (!form.name || !form.mobilenumber || !form.district || !form.typecake || !form.flavor || !form.filling || !form.size || !form.pay || !form.amount || !form.ct_fidelidade) {
        return false
    } else {
        
        return true
    }
}

function getForm() {

    const form = {
        name: document.getElementById("name").value,
        mobilenumber: document.getElementById("mobilenumber").value,
        district: document.getElementById("district").value,
        observacao: document.getElementById("observacao").value,
        typecake: document.getElementById("typecake").value,
        flavor: document.getElementById("flavor").value,
        filling: document.getElementById("filling").value,
        size: document.getElementById("size").value,
        pay: document.getElementById("pay").value,
        amount: document.getElementById("amount").value,
        ct_fidelidade: document.querySelector("input[name='ct_fidelidade']:checked").value,
    }
    
    return form
}

function sendWhatsApp(form) {
    var ct_fidelidade = form.ct_fidelidade ? `*Carto Fidelidade:* ${form.ct_fidelidade}` : "";
    var observacao = form.observacao ? `*Observacao:* ${form.observacao}` : "";
    var whatsappUrl = `https://wa.me/47991399367?text=
    *Esperamos que voc tenha um timo dia!%0a Seu pedido est sendo processado. At logo! *
    %0a%0a
    *Nome:* ${form.name}%0a
    *Telefone:* ${form.mobilenumber}%0a
    *Bairro:* ${form.district}%0a
    *Pagamento:* ${form.pay}%0a
    *Tipo do Bolo:* ${form.typecake}%0a
    *Sabor:* ${form.flavor}%0a
    *Recheio:* ${form.filling}%0a
    *Tamanho:* ${form.size}%0a
    *Quantidade:* ${form.amount}%0a
    ${ct_fidelidade}%0a
    ${observacao}`;
    

    
    window.open(whatsappUrl, "_blank").focus();
}

const Formulario = () => {

    const handleSubmit = (event) => {
        event.preventDefault();

        const form = getForm()
        if (validateForm(form)) {
            sendWhatsApp(form)
        } else {
            alert("Todos os campos so obrigatrios exceto descrio.");
        }
    }

    const handlePhoneChange = (event) => {
        const value = event.target.value;
        event.target.value = value
            .replace(/\D/g, "")
            .replace(/(\d{2})(\d)/, "($1) $2")
            .replace(/(\d{5})(\d)/, "$1-$2");
    };


    //Estrutura Formulario//
    return (


        <div className="container">
            <h1>FAA SEU PEDIDO</h1>
            <form onSubmit={handleSubmit}>

                <div className='containerInput'>
                    <input type="text" id="name" placeholder="Seu nome" />
                </div>

                <div className='containerInput'>
                    <input type="text" id="mobilenumber" placeholder="(99) 9999-9999" onChange={handlePhoneChange} inputMode="numeric"   maxLength="15"/>
                </div>

                <div className='selectContainer'>
                    <select type="text" id="district" placeholder='Bairro' >
                        <option value="" disabled selected>Selecione o Bairro</option>
                        <option value="Paranaguamirim">Paranaguamirim</option>
                        <option value="Aventureiro">Aventureiro</option>
                        <option value="Itaum">Itaum</option>
                    </select>
                </div>

                <div className='selectContainer'>
                    <select type="text" id="pay" placeholder="Forma de Pagamento" >
                        <option value="" disabled selected>Selecione a Forma de Pagamento </option>
                        <option value="Pix">Pix</option>
                        <option value="Dinheiro">Dinheiro</option>
                        <option value="Cartao">Carto</option>
                    </select>
                </div>

                <div className='selectContainer'>
                    <select type="text" id="typecake" placeholder="Tipo do Bolo" >
                        <option value="" disabled selected>Selecione o Tipo do Bolo</option>
                        <option value="Bolo no Pote" >Bolo no Pote</option>
                        <option value="Dinheiro">Bolo de Aniversario</option>
                    </select>
                </div>

                <div className='selectContainer'>
                    <select type="text" id="flavor" placeholder="Sabor" >
                        <option value="" disabled selected>Selecione um Sabor</option>
                        <option value="Chocolate" >Chocolate</option>
                        <option value="Neutro">Neutro</option>
                    </select>
                </div>

                <div className='selectContainer'>
                    <select type="text" id="filling" placeholder="Recheio">
                        <option value="" disabled selected>Selecione o Recheio</option>
                        <option value="Ovomaltine">Ovomaltine</option>
                        <option value="Nutela">Nutela</option>
                        <option value="Abacaxi">Abacaxi</option>
                        <option value="Morango">Morango</option>
                    </select>
                </div>

                <div className='selectContainer'>
                    <select type="text" id="size" placeholder="Tamanho" >
                        <option value="" disabled selected>Selecione o Tamanho</option>
                        <option value="Pequeno">Pequeno</option>
                        <option value="Medio">Medio</option>
                        <option value="Grande">Grande</option>
                    </select>
                </div>

                <div className='selectContainer'>
                    <select type="number" id="amount" placeholder="Quantidade" required>
                        <option value="" disabled selected>Selecione a Quantidade</option>
                        {[...Array(10)].map((_, i) => (
                        <option value={i + 1}>{i + 1}</option>
                        ))}
                    </select>
                </div>



                <div className="form-group">
                    <p>Quer receber carto fidelidade ?</p>
                    <div className="opt_ct_fidelidade">
                        <div className="form-check">
                         <input type="radio" name="ct_fidelidade" id="sim" value="Sim"/>
                            <label htmlFor="sim">Sim</label>
                        </div>
                        <div className="form-check">
                          <input type="radio" name="ct_fidelidade" id="nao" value="No"/>
                            <label htmlFor="nao">No</label>
                        </div>
                    </div>
                </div>
                <div className='containerObs'>
                    <textarea id="observacao" cols="28" rows="5" placeholder='Observao'/>
                </div>
                <button sx={{
                    backgroundColor: "#f73378"
                    
                    
                    }} 
                    className='bt-pedido' 
                    variant="contained" 
                    type="submit" 
                    value="Enviar Pedido">
                        Enviar
                </button>
            </form>
           
        </div>
    )
    //Fim//

}

export default Formulario;