import './Navbar.css';
import { AiOutlineBars, AiOutlineClose } from 'react-icons/ai';
import { Component } from 'react';


class Navbar extends Component {
  state = { clicked: false };
 
  render() {
    return (
      <div className="nav-bg">
        <nav>
          <a href="#">
          <img className='logo-icon' src={""} alt="Adicionar Logo Aqui" /> 
          </a>
          <div>
            <ul id="navbar" className={this.state.clicked ? "#navbar active" : "#navbar"}>
              <li><a className="active" href="#">Fazer Pedido</a></li>
              <li><a href="#">Inicio</a></li>
              <li><a href="#">Contato</a></li>
              <li><a href="#">Fotos</a></li>
              <li><a href="#">Sobre</a></li>
            </ul>
          </div>
          <div id="mobile" onClick={this.handleClick}>
          <i id='bar' onClick={() => this.setState({ clicked: !this.state.clicked })}>
              {this.state.clicked ? <AiOutlineClose style={{ color: 'red', fontSize: '1.5em' }} /> : <AiOutlineBars style={{ fontSize: '1.5em', color:'#552610' }}  />}

            </i>
          </div>
        </nav>
      </div>
    )
  }
}

export default Navbar;