import { motion } from 'framer-motion'
import { useState, useEffect, useRef } from 'react'
import bolo1 from '../../assets/Gallery/bolo-1.jpg'
import bolo2 from '../../assets/Gallery/bolo-2.jpg'
import bolo3 from '../../assets/Gallery/bolo-3.jpg'
import bolo4 from '../../assets/Gallery/bolo-4.jpg'
import bolo5 from '../../assets/Gallery/bolo-5.jpg'
import './Gallery.css'

const imagesbolo = [bolo1, bolo2, bolo3, bolo4, bolo5]

export default function Gallery() {
    const carousel = useRef();
    const [width, setWidth] = useState(0)

    useEffect(() => {
        console.log(carousel.current?.srcollWidth, carousel.current?.offsetWidth)
        setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth)
    }, [])
    return (
        <div className='gallery-center'>
            <h1>Galeria</h1>
        
        <div className='gallery'>
            
            <motion.div
                ref={carousel}
                className='carousel'
                whileTap={{ cursor: 'grabbing' }}
            >
                <motion.div
                    className='inner'
                    drag='x'
                    dragElastic={0.2}
                    dragConstraints={{ left: -width, right: 0 }}
                    initial={{ x: 100 }}
                    animate={{ x: 0 }}
                    transition={{duration: 0.8}}

                >
                    {imagesbolo.map((image) => (
                        <motion.div className='item-img' key={image}>
                            <img src={image} alt='Texto Image' />
                        </motion.div>
                    ))}
                </motion.div>
            </motion.div>
        </div>
        </div>
    )
}